}

.container {
  width: 50%;
  background-color: #f5fcff;
  border-radius: 10px;
  align-self: center;
  margin: 100px auto;
  padding-bottom: 50px;
  display: block;
  box-shadow: 5px 10px 20px #000;
}

.app-title {
  width: 100%;
  height: 150px;
  border-radius: 10px 10px 0 0;
