  align-items: center;
  display: flex;
}

.weather-icon {
  width: 150px;
  height: 150px;
  background-color: #add8e6;
  border: 2px solid #6faac2;
  border-radius: 25%;
  margin-top: 2%;
}

.weather-icon > img {
  margin: 0 auto;
  display: block;
}

